<template>
  <header>
    <div class="top_tit">
      <div class="title" @mouseover.stop="over" @mouseout.stop="over" @click="this.isFix = !this.isFix">
        <p>1단원</p>
      </div>
      <div class="tit_name" v-show="isTitleName">
        <span>단원 도입</span>
      </div>
    </div>
    <div class="top_sub" v-show="!isTitleName">
      <span v-for="(list, i) in subList" :class="this.isSelect[i] ? 'on' : null" :data-page=(i+1) @click="sub">{{ list }}</span>
    </div>
    <button class="top_back btn close">교과서 9쪽 바로 가기</button>
    <button class="top_home btn">Home</button>
  </header>
</template>

<script>
  export default {
    data() {
      return{
        isFix: false,
        isTitleName: false,
        isSelect: [],
        subList: []
      }
    },
    mounted() {
      book[0].lesson[0].page.forEach(element => {
        if(typeof element == 'string'){
          this.subList.push(element);
          this.isSelect.push(false);
        }
      });
      this.isSelect[0] = true;
    },
    methods: {
      over() {
        if(!this.isFix) this.isTitleName = !this.isTitleName;
      },
      sub({target}) {
        for (let i = 0; i < this.isSelect.length; i++){
          this.isSelect[i] = false;
        }
        this.isSelect[+target.dataset.page-1] = true;
      }
    }
}
</script>

<style scoped>
header{
  display: flex;
  align-items: center;
  width: 100%;
  height: 48px;
  background: #c78b4b;
  font-family: NSKRM;
  color: #fff;
  position: relative;
  overflow: hidden;
  cursor: default;
}
header > * {
  height: 38px;
}
.title{
  padding-left: 15px;
  display: flex;
  width: 124px;
  background-color: #555;
  border-radius: 0 38px 38px 0;
  color: #fff;
  position: relative;
  z-index: 2;
}
.title > p {
  margin-left:15px;
  font-size:23px;
  line-height:35px;
  pointer-events: none;
}
.tit_name {
  height: 48px;
  padding: 0px 40px 0 160px;
  font-size: 23px;
  font-family: NSB;
  line-height: 48px;
  border-radius: 0 24px 24px 0;
  background: rgb(255,255,255) linear-gradient(180deg, rgba(0,0,0,0) 80%, rgba(0,0,0,.10968137254901966) 100%);
  box-shadow: 0px 0px 8px 0px rgb(0 0 0 / 38%);
  color: #000;
  position: absolute;
  top: 0;
  z-index: 1;
}
.top_sub {
  padding-left: 16px;
  display: flex;
  gap: 20px;
  font-size: 22px;
}
.top_sub > span {
  padding: 0 20px;
  height: 35px;
  line-height: 34px;
  text-align: center;
}
.top_sub > span.on{
  background-color: #fff;
  border-radius: 35px;
  color: #ba7832;
}
header button {
  border: 1px solid #fff;
  font-family: NSKRR;
  color: inherit;
}
.top_back{
  margin-left: auto;
  width: 187px;
  border-radius: 38px;
  text-align: center;
  font-size: 16px;
  line-height: 36px;
}
.top_home{
  margin: 0 10px;
  padding: 0 5px;
  border-radius: 50%;
}
</style>