<template>
  <section>
    <button :class="['listBtn',{'on': !!listIs}]" @click="listOn">목차</button>
    <ul :class="{'on': !!listIs}">
      <li v-for="books in book" :key="books">
        <p>{{books.title}}</p>
        <dl>
          <dd @click.stop="lessonOn" v-for="lesson in books.lesson" :key="lesson">
            <p>{{lesson.title}}</p>
            <ul :style="{top: this.x+'px'}">
              <li v-for="activity in lesson.page" :key="activity">
                <p v-if="typeof activity == 'string'">{{activity}}</p>
                <span v-if="typeof activity == 'object'">
                  <em v-html="activity.title"></em>
                </span>
              </li>
            </ul>
          </dd>
        </dl>
      </li>
    </ul>
    <div class="bot_pageMove">
        <button class="prev" @click.stop="activityBtnFn">
          <svg width="8px" height="12px"><path fill="#fff" d="M4.331,6.862 L7.486,9.880 C7.740,10.125 7.745,10.532 7.496,10.782 L6.915,11.365 C6.672,11.612 6.274,11.612 6.028,11.365 L0.938,6.255 C0.692,6.011 0.692,5.611 0.938,5.364 L6.028,0.257 C6.271,0.010 6.669,0.010 6.915,0.257 L7.496,0.840 C7.748,1.090 7.743,1.497 7.486,1.742 L4.331,4.760 C4.331,4.760 2.869,6.040 4.331,6.862 Z"></path></svg>
        </button>
        <p>{{ activityIdx }}</p>
        <button class="next" @click.stop="activityBtnFn">
          <svg width="8px" height="12px">
            <path fill="#fff" d="M3.669,6.862 L0.514,9.880 C0.260,10.125 0.255,10.532 0.503,10.782 L1.085,11.365 C1.328,11.612 1.726,11.612 1.972,11.365 L7.062,6.255 C7.308,6.011 7.308,5.611 7.062,5.364 L1.972,0.257 C1.729,0.010 1.331,0.010 1.085,0.257 L0.503,0.840 C0.252,1.090 0.257,1.497 0.514,1.742 L3.669,4.760 C3.669,4.760 5.131,6.040 3.669,6.862 Z"></path>
          </svg>
        </button>
    </div>
  </section>
</template>

<script>
  export default {
  name: 'homeFooter',
  props: {
    msg: String
  },
  data() {
    return {
      book: this.$book,
      lesson: this.$lesson,
      activity: this.$activity,
      bookNo: this.$bookNo,
      lessonNo: this.$lessonNo,
      activityNo: this.$activityNo,
      activityIdx: this.$activityIdx,
      
      listIs: false,
      active: false,
      dd: null,
      x: 0,
    }
  },
  created() {
    this.activitySetFn();
  },
  mounted() {
    this.dd = document.querySelectorAll('dd')

  },
  methods: {
    listOn() {
      this.listIs = !this.listIs
    },
    lessonOn({target}) {
      for (let i = 0; i < this.dd.length; i++) {
        this.dd[i].classList.remove('on')
      }
      target.classList.add('on')
      const {top} = target.closest('dd').getBoundingClientRect();
      this.x = top;
    },
    activitySetFn() {
      this.lesson = this.book[this.bookNo - 1].lesson;
      this.activity = this.lesson[this.lessonNo - 1].page.filter(p => typeof p == 'object');
      this.activityIdx = this.bookNo + '-' + this.lessonNo + '-' + this.activityNo;
    },
    activityBtnFn({target}) {
      switch (target.classList[0]) {
        case 'prev':
            if (this.activityNo <= this.activity.length) {
              if (this.bookNo == 1 && this.lessonNo == 1 && this.activityNo == 1) {
                this.activitySetFn();
                return
              }
              this.activityNo--;
              if (this.activityNo < 1) {
                this.lessonNo--;
                if (this.lessonNo < 1) {
                  this.bookNo--;
                  this.lessonNo = 1;
                  this.activitySetFn();
                  this.lessonNo = this.lesson.length;
                }
                this.activitySetFn();
                this.activityNo = this.activity.length;
              }
            }
          break;
        case 'next':
            if (this.activityNo <= this.activity.length && this.activityNo >= 1) {
              if (this.bookNo == this.book.length && this.lessonNo == this.lesson.length && this.activityNo == this.activity.length) {
                this.activitySetFn();
                return
              }
              this.activityNo++;
              if (this.activityNo > this.activity.length) {
                this.activityNo = 1;
                this.lessonNo++;
                if (this.lessonNo > this.lesson.length) {
                  this.lessonNo = 1;
                  if (this.bookNo != this.book.length) {
                    this.bookNo++;
                  }
                }
              }
            }
          break;
      }
      this.activitySetFn();
    }
  }
}
</script>

<style scoped>
  section {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 42px;
    background: #c78d4d;
    display: flex;
    align-items: center;
  }
  .listBtn {
    color: #fff;
    width: fit-content;
    height: fit-content;
    padding: 5px 20px;
    font-weight: bold;
    font-size: 20px;
    border-radius: 50px;
    border: 1px solid #fff;
    background: rgba(255, 255, 255, 0.2);
  }
  section>ul {
    width: 550px;
    height: 600px;
    background: skyblue;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    bottom: 46px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    overflow-y: scroll;
  }
  section>ul.on {
    opacity: 1;
    pointer-events: visible;
  }

  section>ul>li {
    width: 100%;
    height: fit-content;
    background: cornflowerblue;
    font-size: 20px;
    font-weight: bold;
    color: #fff;
  }
  p {
    text-align: left;
    pointer-events: none;
  }
  dl {}
  dd {
    text-align: left;
    background: skyblue;
    position: relative;
  }
  dd.on,
  dd:hover {
    background-color: darkorange;
  }
  dd ul {
    width: 50px;
    height: 50px;
    background: #cdbaba;
    position: fixed;
    top: 0;
    left: 550px;
    opacity: 0;
    width: 500px;
    height: fit-content;
    padding: 20px
  }
  dd.on ul {
    opacity: 1;
  }
  dd ul li {}
  dd ul li p {
    color: #945b1e;
  }
  dd ul li span {
    position: relative;
    display: inline-block;
    margin-left: 20px;
  }
  dd ul li span::before {
    content: '';
    width: 5px;
    height: 5px;
    position: absolute;
    top: 10px;
    left: -10px;
    background: #c78b4b;
    border-radius: 50%;
  }


.bot_pageMove{display:flex;align-items: center;position: absolute; right: 0;}
.bot_pageMove p{font-size: 18px;color:#fff;font-family: NSB;margin:0 3px;}
.bot_pageMove span{
    display:inline-block;
    width:23px;
    height:24px;
    cursor: pointer;
    position: relative;
    z-index: 998;
}
.bot_pageMove button{
  border-radius: 50%;
  width: 27px;
  height: 27px;
  border: 1px solid #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  margin:5px;
}
.bot_pageMove .prev{
    padding-right: 2px;
}
.bot_pageMove .next{
    padding-left: 2px;
}
.bot_pageMove .next:hover {
    background-color:rgb(255 255 255 / .2);
}

.bot_pageMove button svg {
  pointer-events: none;
}
</style>